.allsitter {
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  cursor: pointer;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 10px 10px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 15px;
}

.linkstyle {
  text-decoration: none;
  color: black;
  outline: none;
}
.linkstyle:hover {
  text-decoration: none;
  color: black;
}

.sitterListingImg {
  margin-top: 20px;
